html, body {
    height: 100%;
    margin: 0;
    background: rgb(199, 198, 198); 
    font-size: 20px;
  }
  
  .header {
    font-weight: 700; 
    text-align: center;
    margin-bottom: 0;
  }
  
  #container {
    min-width: 320px;;
    height: 100%;
    margin: 0;
    display: flex;
    align-items:center;
    justify-content: center;  
  }
  
  .button { 
    display: block;
    box-sizing: border-box;
    font-family: inherit;
    font-size: 100%;
    background-color: #da3b4a;
    color: #fff;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    padding: 0.5rem 1rem;
    transition: background-color 0.3s ease-out, box-shadow 0.3s ease-out; 
  }
  
  .button:hover {
    background-color: #ff4343;
  }
  
  .button:focus {
    box-shadow: 0 0 0 2px #fd4658 inset, 0 0 0 4px white inset;
  }
  
  .hidden {
    display: none;
  }
  
  .popover {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    position: absolute;
  }
  
  .popover-header, .popover-body {
    margin-top: 0;
    margin-bottom: 0;
  }
  
  .popover-header {
    padding: 0.5rem 0.75rem; 
    font-size: 0.9rem;
    background-color: #f7f7f7;
    border-left: 1px solid #000;
    border-top: 1px solid #000;
    border-right : 1px solid #000;
    border-bottom: 1px solid #ebebeb;  
    border-top-left-radius: calc(0.3rem - 1px);
    border-top-right-radius: calc(0.3rem - 1px);
  }
  .popover-body {  
    padding: 0.5rem 0.75rem;  
    font-size: 0.7rem;
    background-color: #fff;
    border-left: 1px solid #000;
    border-bottom: 1px solid #000;
    border-right : 1px solid #000;
    border-bottom-left-radius: calc(0.3rem - 1px);
    border-bottom-right-radius: calc(0.3rem - 1px);
    position: relative;
    z-index: 0;
  }
  
  .popover-tip {
    content: '';
    width: 100%;
    height: 100%;
    background-color: #fff;
    position: relative;
    left: calc(50% - 10px);
    top: -10px;
    height: 20px;
    width: 20px;  
    border-right:  1px solid #000f;
    border-bottom:  1px solid #000f;
    transform: rotate(45deg);
    z-index: 1;
  }